import { IS_DEV } from './constants';

export type IMicroAppInfo = {
  name: string;
  entry: string;
  [key: string]: any;
}

export type IMicroAppKey = 'app_todo' | 'blog'

type IMicroAppList = {
  [key: string]: IMicroAppInfo
};

const microAppList: IMicroAppList = {
  blog : {
    name: 'blog',
    entry: IS_DEV ? 'http://localhost:3001' : 'https://coolfishstudio.github.io/blog/',
  },
  app_todo : {
    name: 'widget-app-todo',
    entry: IS_DEV ? 'http://localhost:3011' : 'https://coolfishstudio.github.io/widget-app-todo/',
  }
};

export const getMicroAppInfo = (key: IMicroAppKey) => {
  return microAppList[key];
};
